<template>
  <div id="protect">
  </div>
</template>

<script setup>
import { ref, onMounted  } from 'vue';
import { supabase } from '../clients/supabase';

import { useRouter } from 'vue-router';
const $router = useRouter();

async function check() {
  var tempL = '';
  var tempP = '';
  
  tempL = prompt("Username:");
  tempP = prompt("Password:");
  
  
  const { data, error } = await supabase.auth.signInWithPassword({
    email: tempL,
    password: tempP,
  })
  
  if (error) {
    alert (error.message)
  } else {
    $router.push('/home')
  }
}

onMounted(() => {
  check()
});
</script>

